.gallery-box {
  text-align: center;
  overflow: hidden;
  position: relative;
}

.gallery-box:after {
  content: "";
  width: 100%;
  height: 100%;
  background: #13b4ef;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.gallery-box:hover:after {
  opacity: 0.5;
}

.gallery-box img {
  width: 100%;
  height: auto;
  transition: all 0.3s ease-in-out;
}

.gallery-box:hover img {
  -moz-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.gallery-box .gallery-icon-container {
  width: 100%;
  height: 100%;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.gallery-box .gallery-icon-container li {
  display: inline-block;
}

.gallery-box .gallery-icon-container li a {
  display: inline-block;
  width: 48px;
  height: 48px;
  line-height: 48px;
  border-radius: 50%;
  position: absolute;
  margin: 0 auto;
  top: -50%;
  margin-left: -24px;
  margin-top: -24px;
  border: 1px solid #fff;
  font-size: 20px;
  color: #fff;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.gallery-box:hover .gallery-icon-container li a {
  top: 50%;
  opacity: 1;
}

.gallery-box .gallery-icon-container li a:hover {
  background: #fff;
  color: #13b4ef;
}

