}

.gallery-carousel .owl-nav .owl-prev {
  left: -3%;
  position: absolute;
  padding: 0;
  top: 50%;
  width: 42px;
  font-size: 48px;
  height: 48px;
  margin-top: -24px;
  color: #13b4ef;
  background: transparent;
  text-align: left;
  transition: all 0.3s ease-in-out;
}

.gallery-carousel .owl-nav .owl-prev:hover {
  color: #5c96bb;
}

.gallery-carousel .owl-nav .owl-next {
  right: -3%;
  position: absolute;
  padding: 0;
  top: 50%;
  width: 42px;
  font-size: 48px;
  height: 48px;
  margin-top: -24px;
  color: #13b4ef;
  background: transparent;
  text-align: right;
